import { FC, ReactNode, useRef } from 'react';
import ProTable, { ActionType, ProColumns } from '@ant-design/pro-table';
import ProCard from '@ant-design/pro-card';
import {
  Tree,
  Spin,
  Table,
  message,
  Select,
  Typography,
  Popconfirm,
  Button,
} from 'antd';
import { useTranslation } from 'react-i18next';
import {
  FindRoleListDocument,
  useFindRoleListQuery,
  useFindRouterAllQuery,
  useFindRouterRoleListQuery,
} from 'generator/auth-center.operation';
import { APP_NAME } from 'data/static';
import { differenceBy, get, keys, toInteger, toString } from 'lodash';
import {
  Maybe,
  RoleFragment,
  RouterFragment,
  RouterRoleFragment,
} from 'generator/auth-center';
import { useImmer } from 'use-immer';
import {
  filtersToWhere,
  generatorColumns,
  gqlErrorMessage,
  proSortToOrder,
  WhereType,
} from 'utils/antd-helper';
import { UseFetchDataAction } from '@ant-design/pro-table/lib/typing';
import { Link } from 'react-router-dom';
import Bluebird from 'bluebird';
import listToTree from 'l2t';
import { nothing } from 'immer';
import './role.less';
const { Option } = Select;
const { Title } = Typography;

type SetColumnType = { key: keyof RoleFragment; [p: string]: any };
type FilterType = {
  [p in keyof RoleFragment]: WhereType;
};
const whereModel: FilterType = {
  roleCode: '_like',
  roleName: '_like',
};

const Role: FC = () => {
  const { t } = useTranslation();

  const [roleId, setRoleId] = useImmer<Maybe<string>>(null);

  const roleData = useFindRoleListQuery({ skip: true });

  const [appName, setAppName] = useImmer<string>('');

  const columnsDefault = generatorColumns<RoleFragment>(FindRoleListDocument, [
    {
      key: 'id',
      render: (
        text: ReactNode,
        record: RoleFragment,
        index: number,
        action: UseFetchDataAction<RoleFragment>
      ) => <Link to={`/role/${text}`}>{text}</Link>,
    },
  ] as SetColumnType[]) as ProColumns<RoleFragment>[];
  /**
   * 定义列
   */
  const [columns] = useImmer(columnsDefault);

  const refProTable = useRef<ActionType>();

  /**
   * 更新保存路由对应的角色
   */
  const handleConfirm = async () => {
    // const delList = differenceBy(
    //   oldRouterRoleList,
    //   proTableSel?.map((p) => ({ roleId: p })) as any,
    //   'roleId'
    // );
    // const addList = differenceBy(
    //   proTableSel?.map((p) => ({ roleId: p })),
    //   oldRouterRoleList as any,
    //   'roleId'
    // );
    // const savePromise = {
    //   bulkSave:
    //     addList && addList.length > 0
    //       ? routerRoleBulk({
    //           variables: {
    //             param: addList.map((p) => {
    //               return {
    //                 roleId: p.roleId,
    //                 routerId: routerItem,
    //               };
    //             }),
    //           },
    //         })
    //       : undefined,
    //   destroy:
    //     delList && delList.length > 0
    //       ? routerRoleDestroy({
    //           variables: {
    //             where: {
    //               id: delList.map((p) => p?.id) as Array<string>,
    //             },
    //           },
    //         })
    //       : undefined,
    // };
    // return Bluebird.props(savePromise)
    //   .then((result) => {
    //     message.success(`${t('finish')}${t('success')}`);
    //   })
    //   .catch(gqlErrorMessage);
  };

  const [treeData, setTreeData] = useImmer<
    Maybe<
      Array<
        Maybe<{
          value: string;
          title: string;
          parentId?: string | undefined;
          key: string;
          children: any;
        }>
      >
    >
  >(null);

  const routerData = useFindRouterAllQuery({ skip: true });

  const listToTreeData = (data: Maybe<Array<RouterFragment>>) => {
    setTreeData((draft) => {
      if (!data || data.length === 0) {
        return nothing as any;
      }
      return listToTree<
        RouterFragment,
        { id: string; parentId: string; children: [] }
      >(data, 'id', 'parentId', 'children', (item) => ({
        value: item?.id,
        title: item?.displayTxt,
        parentId: item?.parentId,
        key: item.id,
      }));
    });
  };

  const handleSelectSelect = (value: any) => {
    routerData
      .refetch({
        param: {
          where: { appName: value },
        },
      })
      .then((result) => {
        setTreeData((draft) => {
          return listToTreeData(result.data.routerAll as any);
        });
      });
    setAppName((draft) => value.toString());
    refProTable.current?.reload();
  };

  const routerRoleData = useFindRouterRoleListQuery({ skip: true });

  /**
   * 原始选择数据
   */
  const [oldRouterRoleList, setOldRouterRoleList] = useImmer<
    Maybe<
      ReadonlyArray<
        Maybe<{ readonly __typename?: 'RouterRole' } & RouterRoleFragment>
      >
    >
  >([]);

  const [proTableSel, setProTableSel] = useImmer<
    Maybe<Array<Maybe<string> | undefined>>
  >([]);

  /**
   * role table 点击事件
   */
  const handleRoleChange = (tempRoleId: string) => {
    if (!tempRoleId) {
      setOldRouterRoleList((draft) => []);
      setProTableSel((draft) => []);
      return;
    }

    routerRoleData
      .refetch({
        param: {
          where: {
            roleId: tempRoleId,
          },
        },
      })
      .then((result) => {
        const routerRoleList = result.data.routerRoleAll
          ?.filter((p) => p?.routerId)
          .map((p) => p?.routerId);
        setOldRouterRoleList((draft) => result.data.routerRoleAll);
        setProTableSel((draft) => routerRoleList);
      });
  };

  return (
    <>
      <ProCard split="vertical">
        <ProCard colSpan="60%">
          <div>
            <Title level={5}>{t('appName')}</Title>
            <Select style={{ width: '100%' }} onSelect={handleSelectSelect}>
              {keys(APP_NAME).map((p) => {
                return (
                  <Option key={p} value={p}>
                    {get(APP_NAME, p)}
                  </Option>
                );
              })}
            </Select>
          </div>
          <div>
            <ProCard title={t('RoleList')} size="small">
              <ProTable<RoleFragment>
                columns={columns}
                actionRef={refProTable}
                request={async (params, sorter, filter) => {
                  return roleData
                    .refetch({
                      param: {
                        limit: params.pageSize,
                        offset:
                          (toInteger(params.current) - 1) *
                          toInteger(params.pageSize),
                        order: proSortToOrder(sorter),
                        where: {
                          appName,
                          ...filtersToWhere([params, filter], whereModel),
                        },
                      },
                    })
                    .then((result) => {
                      return {
                        data: result.data.roleAll as Array<RoleFragment>,
                        total: toInteger(result.data?.roleCount),
                        success: true,
                      };
                    });
                }}
                rowKey="id"
                pagination={{
                  showQuickJumper: true,
                  hideOnSinglePage: true,
                }}
                search={{
                  layout: 'vertical',
                }}
                dateFormatter="string"
                toolBarRender={false}
                debounceTime={toInteger(process.env.REACT_APP_ANTD_DEBOUNCE)}
                options={{ density: true }}
                onRow={(record) => {
                  return {
                    onClick: () => {
                      setRoleId((draft) => record.id);
                      handleRoleChange(toString(record.id));
                    },
                  };
                }}
                rowClassName={(record) => {
                  return record.id === roleId ? 'split-row-select-active' : '';
                }}
              />
            </ProCard>
          </div>
        </ProCard>
        <ProCard title={t('RouterMapper')} headerBordered>
          <div style={{ minHeight: 360 }}>
            <Tree
              checkable
              className="draggable-tree"
              key="value"
              blockNode
              treeData={treeData as any}
              checkedKeys={proTableSel as Array<string>}
            />
          </div>
          <Popconfirm title={t('sureSave')} onConfirm={handleConfirm}>
            <Button disabled={!(appName && roleId)} type="primary">
              {t('save')}
            </Button>
          </Popconfirm>
        </ProCard>
      </ProCard>
    </>
  );
};

export default Role;
